import Vue from 'vue'
import Vuex from 'vuex'

import axios from "axios";
import router from "../router";
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    darkMode: false,
      organisations: [],
      championship: [],
      diplomcat: [],
      sporttype: []

  },
  mutations: {
  },
  actions: {

    postLogin(_, cre) {
      axios.post('/login/', cre)
          .then(res => {
            localStorage.setItem("token", res.data.access)
            router.push('/')
          })
    },

    //CRUD BEGIN//
    //championship start
    getChempoionship({ state }) {
      axios.get('championships')
          .then(res => {
            state.organisations = res.data
          })
    },
    postChampionship(_, cre) {

      axios.post('/championships/',
          cre, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
              "Content-Type": "application/json",
            }
          })
          .then(res => {
            console.log(res)
            this.id = res
            router.push('/championship')
          })
          .catch(error => {
            console.log(error.response.data)
          })
    },

    putChampionship(_, cre) {

      axios.put(`/championships/${cre.id}`,
          cre.id, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
              "Content-Type": "application/json",
            }
          })
          .then(res => {
            console.log(res)
            this.id = res
            router.push('/championship')
          })
          .catch(error => {
            console.log(error.response.data)
          })
    },
    //championshipend

    //championship start
    diplomCatGet({ state }) {
      axios.get('/diploma-categories/')
          .then(res => {
            state.diplomcat = res.data
          })
    },
    diplomCatPosts(_, cre) {

      axios.post('/diploma-categories/',
          cre, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
              "Content-Type": "application/json",
            }
          })
          .then(res => {
            console.log(res)
            this.id = res

            router.push('/diplomcategory')
          })
          .catch(error => {
            console.log(error.response.data)
          })
    },

    diplomCat(_, cre) {

      axios.put(`/diploma-categories/${cre.id}/`,
          cre.data, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
              "Content-Type": "application/json",
            }
          })
          .then(res => {
            console.log(res)
            this.id = res
            router.push('/diplomcategory')
          })
          .catch(error => {
            console.log(error.response.data)
          })
    },
    //diplomCat


    //championship start
    getorganizations({ state }) {
      axios.get('organizations')
          .then(res => {
            state.championship = res.data
          })
    },
    postorganizations(_, cre) {

      axios.post('/organizations/',
          cre, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
              "Content-Type": "application/json",
            }
          })
          .then(res => {
            console.log(res)
            this.id = res
            router.push('/organizsation')
          })
          .catch(error => {
            console.log(error.response.data)
          })
    },

    putorganizations(_, cre) {

      axios.put(`/organizations/${cre.id}/`,
          cre.data, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
              "Content-Type": "application/json",
            }
          })
          .then(res => {
            console.log(res)
            this.id = res
            router.push('/organizsation')
          })
          .catch(error => {
            console.log(error.response.data)
          })
    },
    //championshipend

    //championship start
    getsporttype({ state }) {
      axios.get('sport-types')
          .then(res => {
            state.sporttype = res.data
          })
    },
    postsporttype(_, cre) {

      axios.post('/sport-types/',
          cre, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
            }
          })
          .then(res => {
            console.log(res)
            this.id = res
            router.push('/sport_type')
          })
          .catch(error => {
            console.log(error.response.data)
          })
    },

    putsporttype(_, cre) {

      axios.put(`/sport-types/${cre.id}/`,
          cre.data, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
            }
          })
          .then(res => {
            console.log(res)
            this.id = res
            router.push('/sport_type')
          })
          .catch(error => {
            console.log(error.response.data)
          })
    },
    //championshipend

  },
  modules: {
  }
})
